.infoBar {
  width: 100vw;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  color: black;
  height: 40px;
  z-index: -1;
}

.infoBar li, p {
  list-style: none;
  margin: auto;
  font-size: 17px;
}

@media only screen and (max-width: 850px) {
  .infoBar {
    width: 100vw;
    padding: 0px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: rgba(255, 255, 255, 0.5);
    color: black;
    height: 70px;
    z-index: -1;
  } .infoBar li, p {
    margin-left: auto;
    margin-right: auto;
    font-size: 17px;
  } .infoBar li {
    float: left;
  }
  
}